class Circle {
    constructor(x, y, radius,imgObj,angle) {
        this.x = x;
        this.y = y;
        this.radius = radius;
        this.imgObj = imgObj;
        this.angle = angle;
    }
    
    paint(context) {
        context.save();
        context.fillStyle = this.fillStyle;
        context.beginPath();
        context.arc(this.x, this.y, this.radius, 0, Math.PI * 2, true);
        /*test*/
        /*this.strokeStyle = 'rgba(0,0,0,1)';
        context.stroke();*/
        /*test*/
        context.restore();
    }
}

/*å¼¹å¤¹åºç±»*/
class Clip {
    constructor(x,y,w,h,imgObj) {
        this.x = x;
        this.y = y;
        this.w = w;
        this.h = h;
        this.imgObj = imgObj;
    }

    paint(context) {
        //å¼¹å¤¹ç»å¶æä¸ä½¿ç¨
        context.save();
        context.translate(this.x,this.y);
        context.beginPath();
        context.moveTo(0,0);
        context.lineTo(0,this.w);
        context.lineTo(this.w,this.h);
        context.lineTo(0,this.h);
        context.closePath();
        /*context.strokeStyle = "rgba(0,0,0,1)";
        context.stroke();*/
        if (this.imgObj) {
            context.drawImage(this.imgObj,0,0,this.w,this.h);
        }
        context.restore();
    }
}

/*å­å¼¹åºç±»*/
class imgBullet {
    constructor(x,y,w,h,z/*çºµè½´åç§»é*/,imgObj,angle) {
        this.x = x;
        this.y = y;
        this.w = w;
        this.h = h;
        this.z = z;
        this.imgObj = imgObj;
        this.angle = angle;
    }

    paint(context) {
        context.save();
        context.translate(this.x,this.y);
        context.rotate(this.angle+Math.PI*1.5);
        context.beginPath();
        context.moveTo(-this.w/2, -this.z);
        context.lineTo(this.w/2, -this.z);
        context.lineTo(this.w/2,this.h-this.z);
        context.lineTo(-this.w/2,this.h-this.z);
        context.closePath();
        /*context.strokeStyle = "rgba(0,0,0,1)";
        context.stroke();*/
        if (this.imgObj) {
            context.drawImage(this.imgObj,-1*this.w/2,-this.z,this.w,this.h);
        }
        context.restore();
    }

    update() {

    }
}

