    //ç¸äº¤æ£æµ
    crossMul(v1,v2) {
        return   v1.x*v2.y-v1.y*v2.x;
    }
    //ç¸äº¤è¿åtrue
    checkCross(p1,p2,p3,p4) {
      var v1={x:p1.x-p3.x,y:p1.y-p3.y},
      v2={x:p2.x-p3.x,y:p2.y-p3.y},
      v3={x:p4.x-p3.x,y:p4.y-p3.y},
      v=this.crossMul(v1,v3)*this.crossMul(v2,v3)
      v1={x:p3.x-p1.x,y:p3.y-p1.y}
      v2={x:p4.x-p1.x,y:p4.y-p1.y}
      v3={x:p2.x-p1.x,y:p2.y-p1.y}
      // console.log("v: "+v+" ; "+"distance: "+this.crossMul(v1,v3)*this.crossMul(v2,v3))
      if (v<=50000&&this.crossMul(v1,v3)*this.crossMul(v2,v3)<=50000) {}
      return (v<=0&&this.crossMul(v1,v3)*this.crossMul(v2,v3)<=0)?true:false
    }
    //ç¢°ææ£æµ
    checkCollision(circleBullet) {
        var Bx = circleBullet.x-circleBullet.w*0.5;
        var By = circleBullet.y-circleBullet.z;
        var Bw = circleBullet.w;
        var Bh = circleBullet.h;
        var B_l_t = {x:Bx,y:By}
        var B_r_t = {x:Bx+Bw,y:By}
        var B_r_b = {x:Bx+Bw,y:By+Bh}
        var B_l_b = {x:Bx,y:By+Bh}
        for(var i = 0; i < this.rotationCircles.length; i++){
            var Î¸ = this.rotationCircles[i].angle
            Î¸%=2*Math.PI;
            /*Î¸ = Î¸* 180 / Math.PI;*/
            var Ax = this.rotationCircles[i].x;
            var Ay = this.rotationCircles[i].y;
            var Aw = this.rotationCircles[i].w;
            var Ah = this.rotationCircles[i].h;
            var Az = this.rotationCircles[i].z;
            var A_l_t = {x:Ax-Aw/2*Math.sin(Î¸),y:Ay+Aw/2*Math.cos(Î¸)}
            var A_r_t = {x:Ax+Aw/2*Math.sin(Î¸),y:Ay-Aw/2*Math.cos(Î¸)}
            var A_r_b = {x:Ax+(Ah-Az)*Math.cos(Î¸)+Aw/2*Math.sin(Î¸),y:Ay+(Ah-Az)*Math.sin(Î¸)-Aw/2*Math.cos(Î¸)}
            var A_l_b = {x:Ax+(Ah-Az)*Math.cos(Î¸)-Aw/2*Math.sin(Î¸),y:Ay+(Ah-Az)*Math.sin(Î¸)+Aw/2*Math.cos(Î¸)}
            if (
                this.checkCross(B_l_t,B_r_t,A_l_b,A_l_t) || //å·¦
                this.checkCross(B_l_t,B_l_b,A_l_b,A_l_t) || //å·¦
                this.checkCross(B_r_t,B_r_b,A_l_b,A_l_t)    //å·¦
            ) {
                return {isCollision:true,failDirection:"left"}
            }
            if (
                this.checkCross(B_l_t,B_r_t,A_l_b,A_r_b) || //ä¸
                this.checkCross(B_l_t,B_l_b,A_l_b,A_r_b) || //ä¸
                this.checkCross(B_r_t,B_r_b,A_l_b,A_r_b)    //ä¸
            ) {
                return {isCollision:true,failDirection:"bottom"}
            }
            if (
                this.checkCross(B_l_t,B_r_t,A_r_b,A_r_t) || //å³
                this.checkCross(B_l_t,B_l_b,A_r_b,A_r_t) || //å³
                this.checkCross(B_r_t,B_r_b,A_r_b,A_r_t)    //å³
            ) {
                return {isCollision:true,failDirection:"right"}
            }
            if(circleBullet.y-(ROTATION_CENTER.y+RADIUS_BIG)==0){
            }
        }
        return false
    }

